import i18next from 'i18next'

export interface SwapPayBody {
  [key: string]: any

  sourceId: string
  contractAddressa: string
  contractAddressb: string
  swapContractPath: string | undefined // 币种合约地址集合，多个地址用逗号分隔 格式 address1,address2
  methodName: string
  amounta: string
  amountb: string
  minerFee: string
  serviceFee: string
  gasPrice: string
  deadline: number
  contractAddressAScale: number
  contractAddressBScale: number
  feeScale: number
}

export enum SwapPayType {
  SWAP0001 = 'SWAP0001', // 兑换
  SWAP0002 = 'SWAP0002', // SWAP0002添加资金池
  SWAP0003 = 'SWAP0003' // 移除资金池
}

/**
 * 兑换发起
 * @param type
 * @param payBody
 */
export function SwapForDiDi(type: SwapPayType, payBody: SwapPayBody) {
  return new Promise(function(resolve, reject) {
    window.JSBridge.invoke(
      'SWAP',
      {
        type: type, // SWAP0001兑换 SWAP0002添加资金池 SWAP0003移除资金池
        data: payBody
      },
      (res, error) => {
        if (res) {
          if (res.code === '200') {
            resolve(res)
          } else {
            reject(res)
          }
        } else {
          if (error === 'cancel') {
            reject({ msg: i18next.t('cancel'), error: error })
          } else {
            reject({ msg: i18next.t('Network error'), error: error })
          }
        }
      }
    )
  })
}
