import axios from '../axios'
import { CurrencyAmount, JSBI, Token, TokenAmount } from '@iccswap/sdk'
import { parseUnits } from '@ethersproject/units'

/**
 * DIDI 接口支持的methodName
 */
export enum methodNames {
  addLiquidity = '添加流动性',
  addLiquidityETH = '添加ETH交易对流动性',
  removeLiquidityWithPermit = '移除流动性',
  removeLiquidityETHWithPermit = '移除ETH交易对流动性',
  swapETHForExactTokens = '用ETH兑换精确的代币',
  swapExactETHForTokens = '用精确的ETH交换代币',
  swapExactTokensForETH = '用精确的代币兑换ETH',
  swapExactTokensForTokens = '用精确的代币兑换代币',
  swapTokensForExactETH = '用代币兑换精确的ETH',
  swapTokensForExactTokens = '用代币兑换准确的代币'
}

export interface DiDiTxFee {
  gasFee: string
  gasLimit: number
  gasPrice: string
  serviceFee: string
}

export interface DiDiTxFeeParams {
  [key: string]: any

  address: string | undefined // 支付地址
  amountIn: string | undefined // 扣除资金代币数量
  amountOut: string | undefined // 预期兑换资金代币数量
  methodName: string | undefined // 合约方法名
  swapContractPath: string | undefined // 币种合约地址集合，多个地址用逗号分隔 格式 address1,address2
  tokenIn: string | null // 扣除资金代币Token ETH为空
  tokenOut: string | null // 兑换资金代币Token ETH为空
  deadline: number | null // 截止时间(时间戳)
}

/**
 * 查询用户ETH余额
 *  ps. 保留小数scale对应币种的decimal，如果这两个值不对应会不会出问题？
 */
export async function getUserETHBalance(): Promise<CurrencyAmount | undefined> {
  const res = await axios({
    url: '/getUserCurrencyBalance',
    method: 'post',
    params: {
      contractAddress: ''
    }
  })
  if (res.code === '200') {
    return CurrencyAmount.ether(JSBI.BigInt(parseUnits(res.data.balance, res.data.scale).toString()))
  } else {
    return Promise.reject(res)
  }
}

/**
 * 查询币种余额
 * ps. 保留小数scale对应币种的decimal，如果这两个值不对应会不会出问题？
 * @param token
 */
export async function getUserCurrencyBalance(token: Token): Promise<TokenAmount | undefined> {
  const res = await axios({
    url: '/getUserCurrencyBalance',
    method: 'post',
    params: {
      contractAddress: token.address.toString()
    }
  })
  if (res.code === '200') {
    const amount = JSBI.BigInt(parseUnits(res.data.balance, res.data.scale).toString())
    return new TokenAmount(token, amount)
  } else {
    return Promise.reject(res)
  }
}

/**
 * 查询多个币种余额
 * @param tokens
 */
export async function getUserCurrencyBalances(
  tokens: (Token | undefined)[]
): Promise<{ [tokenAddress: string]: TokenAmount | undefined }> {
  const o: { [tokenAddress: string]: TokenAmount | undefined } = {}
  for (let index = 0; index < tokens.length; index++) {
    const token = tokens[index]
    if (token) {
      const amount = await getUserCurrencyBalance(token)
      amount && (o[token.address] = amount)
    }
  }
  return o
}

/**
 * 预估算矿工费 兑换交易
 * @param [number] deadline
 * @param arg
 */
export async function queryTxFeeSwapTransaction({ deadline, ...arg }: DiDiTxFeeParams): Promise<DiDiTxFee | undefined> {
  return new Promise((resolve, reject) => {
    axios({
      url: '/queryTxFeeSwapTransaction',
      method: 'post',
      params: {
        deadline: deadline,
        ...arg
      }
    })
      .then((res: any) => {
        if (res.code === '200') {
          resolve(res.data)
        } else {
          reject(res)
        }
      })
      .catch(reject)
  })
}
